import DoAxes from "@/libs/doAxes.js";
/* eslint-disable */
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'TimeSlider',
  props: {
    n: {
      type: Object,
      default: function _default() {
        return {
          t: 72.5,
          l: 81.4,
          k: 500
        };
      } // Ratio fontSize px to Width px, where t for time text,l for ticks text, k is for stroke

    },
    ds: {
      type: Object,
      default: function _default() {
        return {
          w: 2000,
          h: 120,
          // for viewBox( 0,0,w,h)
          y: 0.7125,
          // Percent of height (h) where axis placed 0.50 means middle
          tmy: 0.201,
          // Percent of height (h) where current time label placed
          tsz: 0.06,
          // Percent of height (h) ticks length
          lby: 0.93,
          // Percent of height (h) where ticks labels placed
          mv: 0.06 // Percent of mover size 100 X 100 (thumb)

        };
      } //ds:{w:2000, h:120,  y:0.7125, tmy:0.20, tsz:0.06, lby:0.93, mv:0.06 },

    },
    tickCount: {
      type: Number,
      default: function _default() {
        return 25;
      }
    }
  },
  components: {},
  data: function data() {
    return {
      msg: 'Welcome',
      title: 'TimeSlider',
      error: '',
      tiki: [],
      tmlbl: [{
        "id": 1,
        tm: "06:00",
        tmx: 3.001
      }],
      dragging: false,
      canSliderDrug: false,
      x: 50,
      y: 0.15,
      u: 1,
      // Point size in Pixel
      curTm: "06:00",
      selectedElement: false,
      offset: 0,
      // Mouse offset define in startDrag
      svgSlider: null,
      middleOffset: 0
    };
  },
  computed: {
    mv: function mv() {
      return this.ds.h / 8 * 0.06;
    },
    tmf: function tmf() {
      return this.ds.w * this.u / this.n.t / this.u + "px";
    },
    lbf: function lbf() {
      return this.ds.w * this.u / this.n.l / this.u + "px";
    },
    k: function k() {
      return this.ds.w * this.u / this.n.k / this.u + "px";
    },
    ewd: function ewd() {
      return this.ds.w * this.u + "px";
    }
  },
  mounted: function mounted() {
    window.addEventListener('mouseup', this.stopDrag);
    this.initSvg();
  },
  created: function created() {},
  activated: function activated() {},
  watch: {},
  methods: {
    setNowTime: function setNowTime() {
      var _this = this;

      // Set Current Time
      this.curTm = new Date(Date.now()).toTimeString().slice(0, 5);
      var pos = this.tmlbl.find(function (a) {
        return a.tm == _this.curTm;
      }); //console.log( " Cur getTime" ,this.curTm, " pos ", pos, "middleOffset:",this.middleOffset);

      if (pos) this.x = pos.tmx - this.middleOffset;
    },
    setTicks: function setTicks(tickCount) {
      var doAxes = new DoAxes(); //let of = this.ds.of*this.ds.w;

      var of = this.middleOffset; // Fill Time Label tmlbl ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~

      while (this.tmlbl.length > 0) {
        this.tmlbl.pop();
      }

      ;
      this.tmlbl = doAxes.fillTimeLabel(this.ds.w, of, 1440).map(function (x) {
        return x;
      });

      while (this.tiki.length > 0) {
        this.tiki.pop();
      }

      ;
      this.tiki = doAxes.fillAxisLabel(of, 1440, tickCount).map(function (x) {
        return x;
      });
    },
    initSvg: function initSvg(event) {
      if (event) {
        this.svgSlider = event.target;
      } else if (!event || !this.svgSlider) {
        this.svgSlider = document.getElementById('svg0');
      } else {
        return;
      }

      if (this.svgSlider) {
        var CTM = this.svgSlider.getScreenCTM();
        var moverPos = this.svgSlider.getElementById('pMover').getBoundingClientRect();
        this.u = CTM.a;
        this.middleOffset = moverPos.width / CTM.a / 2;
        this.setTicks(this.tickCount);
        this.setNowTime();
      }
    },
    getMousePosition: function getMousePosition(evt) {
      var CTM = this.svgSlider.getScreenCTM();
      return {
        x: (evt.clientX - CTM.e) / CTM.a,
        y: (evt.clientY - CTM.f) / CTM.d
      };
    },
    startDrag: function startDrag(event) {
      if (event.target.classList.contains('draggable')) {
        this.dragging = true;
        this.selectedElement = event.target;
        this.offset = this.getMousePosition(event); //console.log("startDrag selectedElement ", this.selectedElement);

        this.offset.x -= this.x;
        var sPos = this.selectedElement.getBoundingClientRect();
      }
    },
    stopDrag: function stopDrag() {
      this.dragging = false;
      this.selectedElement = null;
    },
    doDrag: function doDrag(event) {
      event.preventDefault();
      var tag = event.target;

      if (this.dragging) {
        var coord = this.getMousePosition(event);
        var x1 = coord.x - this.offset.x;
        var end = this.tmlbl.slice(-1)[0].tmx - this.middleOffset;

        if (x1 <= end && x1 > 0) {
          this.x = x1;
          this.getDrugTime(this.x);
        }
      }
    },
    getDrugTime: function getDrugTime(x) {
      var m = this.middleOffset + x;
      var row = this.tmlbl.filter(function (a) {
        return a.tmx >= m;
      }).slice(0, 1)[0];
      this.curTm = row.tm;
      this.$emit('current-time', this.curTm);
    }
  }
};